@import '../base/mixins';
@import '../base/variables';

#contact-outer-wrapper {
    background: linear-gradient(#40c4bf, #406D8F);
    height: 100%;
    width: 100%;
    padding: 30px 0;

}

#contact-inner-wrapper {
    width: 80%;
    height: 100%;
    margin: 0 auto;
    margin-top: 30px;
    background-color: white;
    padding: 50px;
    box-sizing: border-box;
    color: $text-dark-grey;
}

#contact-title {
    @include flex-column;
    height: 100px;
}

.twitter {
    color: purple;
    margin-right: 5px;    
}

#contact-details {
    margin: 50px 0;
}